const ADD_MESSAGE = 'ADD-MESSAGE'

let initialState = {
    dialogs: [
        {id:1, name: 'User1',src: 'data:image/jpeg;base64,'},
        {id:2, name: 'User2',src: 'data:image/jpeg;base64,' },
        {id:3, name: 'User3',src: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQzVXrGS2ktg64m7pA-t_VYDcE3ztQeoirVDw&usqp=CAU'},
        {id:4, name: 'User4',src: 'data:image/jpeg;base64,'},
        {id:5, name: 'User5',src: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRYGVZZ5DHlzLQQKQIEpeLzv3zXEgbF-P5OCA&usqp=CAU'}
    ],
    messages: [
        {id:1, message: 'Hi'},
        {id:2, message: "How are you"},
        {id:3, message: 'Im fine'},
        {id:4, message: "How about you"},
        {id:5, message: 'Normal'}
    ]   
}

const dialogsReducer = (state=initialState, action) => {

    switch (action.type) {  
        case ADD_MESSAGE:
            let newMessage = { id:6, message: action.newMessage };    
            return {
                ...state,
                messages: [...state.messages, newMessage]
            }              
        default :
            return state;
    }    
}

export const addMessageActionCreator = (newMessage) => {
    return { type: ADD_MESSAGE, newMessage}    
}

export default dialogsReducer;